 issued by the Russian Chamber of commerce.<br>(viii) Copy of Sellers License to Export.<br>(ix) Copy of Sellers License to Transport.<br>(x) Copy of Certificate of Incorporation.<br>(xi) Transaction / Deal Passport of Product.<br>(xii) Tank Receipts and copy of the Storage Agreement.<br><strong style="color:#030E77;">Step 6.</strong> Buyer issue MT700 Documentary Letter of Credit (top 50 bank or confirmed by a top 50 bank) to Seller Bank payable against presentation of shipping documents complying with the Letter of Credit terms & conditions & a copy of Notice of Readiness (NOR) issued by shipmaster or shipping agent:<br>(i) Commercial Invoice - one original and three copies.<br>(ii) Full set(s) of 3/3 original \'clean on board\' bills of lading plus 3 non-negotiable copies each issued or endorsed to the order and, marked: \'freight payable as per charter party\'.<br>(iii) Certificate of quality analysis issued by SGS  one original and three copies.<br>(iv) Certificate of origin issued by Chamber of Commerce of the Russian Federation  one original and three copies<br><strong style="color:#030E77;">Step 7.</strong> POP is received by Buyer and presented to their bank officer<br>Buyers Bank officer will open DLC MT700<br>Seller load the cargo and issue QQ by SGS<br>Once Vessel has arrived at port of destination, buyer conduct QQ by SGS or similar and release payment by MT 103.<br><br><strong style="color:#030E77;">FOB (HOUSTON/ROTTERDAN)</strong><br><br><p><strong style="color:#030E77;">Step 1.</strong> Buyer issues official ICPO  Irrevocable Corporate Purchase Order to Seller and send POF (Proof of funds) MT799/BCL/RWA; the ICPO must be issued with Buyers Company Letterhead containing Buyers full contact information.<br><strong style="color:#030E77;">Step 2.</strong> Seller verifies Buyers official ICPO and issues Draft Contract and Proforma Invoice of the first delivery to the Buyer.<br><strong style="color:#030E77;">Step 3.</strong> Buyer signs Draft Contract and Proforma Invoice and return with a Letter of Acceptance including a copy of Company License and a Scanned Passport copy of the Buyer.<br><strong style="color:#030E77;">Step 4.</strong> Seller commence process for legalization and Notarization of Contracts via Law firm. Process for Product Document / process for Allocation Transaction Certificate in favor of Buyer as the legitimate beneficiary with legal charges borne by Buyer of US$54,720 through the Russian Government Ministry of Energy Authorized Law Chambers. <br><strong style="color:#030E77;">Step 5.</strong> Seller issue to Buyer the following POP documents:<br>Registered Contract and Allocation Passport Certificate issued by Ministry of Energy.<br>SGS Report. (ONLY BANK TO BANK)<br>Title Certificate.<br>Dip-Test Authorization. (THE REFINERY CONDUCT THE DIP TEST THROUGHOUT SURVEYOR, FORWARD THE RESULTS TO THE BANK)<br>Tank Farm Receipts.<br>Tank Manifest, Storage Port Agreement.<br>Copy of Approval to Export.<br>Copy of Sellers License to Export.<br>Copy of Statement of Availability of the product.<br>Copy of the Refinery Commitment to produce the product.<br>Copy of Transneft Contract and Storage Facility to transport the product to port of loading.<br>Copy of Charter Party Agreements to transport the product to discharge Port.<br>Bill of Lading.<br><strong style="color:#030E77;">Step 6.</strong> After confirmation of SGS - Dip-Test, Buyer pays with TT- Telegraphic Transfer or MT103 to the Seller for the liftable quantity.<br><strong style="color:#030E77;">Step 7.</strong> Buyer lifts product as agreed on Contract.</p><br>', imagen: '20.jpg' },
