          { icono:'mdi-airplane-takeoff',text: 'Jetfuel',module:'JETFUEL' , titulo:'Jet Fuel Price Monitor es una iniciativa conjunta de Platts y la IATA',contenido:'<strong style="color:#030E77;">Combustible de aviacin</strong> - Jet A, Jet A1, JP54, Jet B',imagen:'17.jpg'  },
