          { icono:'mdi-tanker-truck',text: 'Gas', module:'GAS' , titulo:'El gas natural es una fuente de energa conveniente que se canaliza directamente desde los campos de petrleo',contenido:'<strong style="color:#030E77;">Gas natural licuado</strong> - GNL<br><br><strong style="color:#030E77;">Gas licuado de petrleo</strong> - GLP',imagen:'19.jpg'  },
          { icono:'mdi-cogs',text: 'Procedimientos', module:'PROCEDURES1' , titulo:'PROCEDIMIENTOS BSICOS CIF & FOB (HOUSTON/ROTTERDAN)',contenido:'<strong style="color:#030E77;">CIF BASIC PROCEDURES</strong><br><br><strong style="color:#030E77;">Paso 1.</strong> El Comprador emite ICPO oficial - Orden de compra corporativa irrevocable al Vendedor y enva POF (Prueba de fondos) MT799 / BCL / RWA; el OIPC debe emitirse con el Membrete de la Compaa del Comprador que contenga la informacin de contacto completa del Comprador.<br><strong style="color:#030E77;">Paso 2.</strong> El Vendedor verifica el OIPC oficial del Comprador y emite el Contrato borrador y la Factura proforma de la primera entrega al Comprador.<br><strong style="color:#030E77;">Paso 3.</strong> El comprador firma el borrador del contrato y la factura proforma y devulvalo con una carta de aceptacin que incluye una copia de la licencia de la empresa y una copia escaneada del pasaporte del comprador.<br><strong style="color:#030E77;">Paso 4.</strong> El vendedor inicia el proceso para la legalizacin y notarizacin de contratos a travs de un bufete de abogados Proceso para el documento del producto / proceso para el certificado de transaccin de asignacin a favor del Comprador como beneficiario legtimo con cargos legales a cargo del Comprador de US $ 50,450 a travs de la Ley autorizada del Ministerio de Energa del Gobierno de Rusia Cmaras.<br><strong style="color:#030E77;">Paso 5.</strong> Enve por mensajera del vendedor los documentos de prueba parcial de productos a travs de un servicio de mensajera en condiciones de servidumbre al comprador para que los presente a su banco para abrir el DLC como se indica a continuacin:<br>(i) Contrato legalizado por transacciones exitosas de 12 meses.<br>(ii) Certificado de transaccin de asignacin a favor del Comprador emitido por el Ministerio de Energa de Rusia.<br>(iii) Certificado de Atestacin emitido al Vendedor por la Cmara de Comercio de Rusia.<br>(iv) Certificado de Membresa emitido al Vendedor por la Cmara de Comercio de Rusia.<br>(v) Emisin de Declaracin de Disponibilidad de Producto al Comprador por parte del Ministerio de Energa.<br>(vi) Certificacin de Asignacin de producto emitida al Comprador por el Ministerio de Energa.<br>(vii) Certificado de origen del producto emitido por la Cmara de Comercio de Rusia.<br>(viii) Copia de la Licencia de Exportacin del Vendedor.<br>(ix) Copia de la Licencia de Transporte del Vendedor.<br>(x) Copia del Certificado de Incorporacin.<br>(xi) Transaccin / Pasaporte de trato del producto.<br>(xii) Recibos del Tanque y copia del Acuerdo de Almacenamiento.<br><strong style="color:#030E77;">Paso 6.</strong> El comprador emite una carta de crdito documental MT700 (50 bancos principales o confirmada por uno de los 50 bancos principales) al banco vendedor pagadera contra la presentacin de los documentos de envo que cumplan con los trminos y condiciones de la carta de crdito y una copia del Aviso de preparacin (NOR) emitido por el capitn del barco o el agente de envo:<br>(i) Factura comercial: un original y tres copias.<br>(ii) Juego completo (s) de 3/3 conocimientos de embarque originales \'limpios a bordo\' ms 3 copias no negociables, cada una emitida o endosada a la orden y, marcada: \'flete pagadero segn contrato de fletamento\'.<br>(iii) Certificado de anlisis de calidad emitido por SGS: un original y tres copias.<br>(iv) Certificado de origen emitido por la Cmara de Comercio de la Federacin de Rusia: un original y tres copias<br><strong style="color:#030E77;">Paso 7.</strong> El Comprador recibe el POP y lo presenta a su funcionario bancario.<br>El oficial del banco del comprador abrir el DLC MT700<br>El vendedor carga la carga y emite QQ por SGS<br>Una vez que el buque ha llegado al puerto de destino, el comprador realiza la QQ por parte de SGS o similar y libera el pago por MT 103.<br><br><strong style="color:#030E77;">FOB (HOUSTON/ROTTERDAN)</strong><br><p><strong style="color:#030E77;">Paso 1.</strong> El Comprador emite ICPO oficial - Orden de compra corporativa irrevocable al Vendedor y enva POF (Prueba de fondos) MT799 / BCL / RWA; el OIPC debe emitirse con el Membrete de la Compaa del Compradorque contenga la informacin de contacto completa del Comprador.<br><strong style="color:#030E77;">Paso 2.</strong> El Vendedor verifica el OIPC oficial del Comprador y emite el Contrato borrador y la Factura proforma de la primera entrega al Comprador.<br><strong style="color:#030E77;">Paso 3.</strong> El comprador firma el borrador del contrato y la factura proforma y devulvalo con una carta de aceptacin que incluye una copia de la licencia de la empresa y una copia escaneada del pasaporte delcomprador.<br><strong style="color:#030E77;">Paso 4.</strong>            El vendedor inicia el proceso de legalizacin y notarizacin de los contratos a travs de un bufete de abogados. Proceso para Documento de Producto / proceso para Certificado de Transaccin de Asignacina favor del Comprador como beneficiario legtimo con cargos legales a cargo del Comprador de US $ 54,720 a travs de las Cmaras Jurdicas Autorizadas del Ministerio de Energa del Gobierno Ruso.<br><strong style="color:#030E77;">Paso 5.</strong> El Vendedor enva al Comprador los siguientes documentos POP:<br>Certificado de Contrato Registrado y Pasaporte de Asignacin emitido por el Ministerio de Energa.<br>Informe SGS. (SOLO BANCO A BANCO)<br>Certificado de ttulo.<br>Autorizacin de prueba de inmersin. (LA REFINERA REALIZA LA PRUEBA DE INMERSIN A TODO EL SURVEYOR, ENVIAR LOS RESULTADOS AL BANCO)<br>Recibos de la granja de tanques.<br>Manifiesto de tanques, Acuerdo de puerto de almacenamiento.<br>Copia de aprobacin para exportar.<br>Copia de la licencia del vendedor para exportar.<br>Copia de la declaracin de disponibilidad del producto.<br>Copia del Compromiso de Refinera para producir el producto.<br>Copia del contrato de Transneft y la instalacin de almacenamiento para transportar el producto al puerto de carga.<br>Copia de los Acuerdos de Charter Party para transportar el producto al puerto de descarga.<br>Gua de carga.<br><strong style="color:#030E77;">Paso 6.</strong> Despus de la confirmacin de SGS - Dip-Test, el Comprador paga con TT- Transferencia telegrfica o MT103 al Vendedor por la cantidad elevable.<br><strong style="color:#030E77;">Paso 7.</strong> El comprador levanta el producto segn lo acordado en el contrato.</p><br>',imagen:'20.jpg'  },
