          { icono:'mdi-cogs',text: 'Procedimentos', module:'PROCEDURES1' , titulo:'PROCEDIMENTOS BSICOS CIF & FOB (HOUSTON/ROTTERDAN)',contenido:'<strong style="color:#030E77;">CIF BASIC PROCEDURES</strong><br><br><strong style="color:#030E77;">Etapa 1.</strong> Comprador emite ICPO oficial - Pedido de Compra Corporativo Irrevogvel ao Vendedor e envia POF (Prova de fundos) MT799 / BCL / RWA; o ICPO deve ser emitido com o Papel Timbrado da Empresa do Comprador contendo todas as informaes de contato do Comprador.<br><strong style="color:#030E77;">Etapa 2.</strong> O vendedor verifica o ICPO oficial do Comprador e emite a Minuta do Contrato e a Fatura Proforma da primeira entrega ao Comprador.<br><strong style="color:#030E77;">Etapa 3.</strong> O comprador assina a minuta do contrato e a fatura pr-forma e a devolve com uma carta de aceitao, incluindo uma cpia da licena da empresa e uma cpia digitalizada do passaporte do comprador.<br><strong style="color:#030E77;">Etapa 4.</strong> O vendedor inicia o processo de legalizao e notarizao dos contratos por meio do processo de escritrio de advocacia para o documento do produto / processo para o certificado de transao de alocao em favor do comprador como o beneficirio legtimo com encargos legais arcados pelo comprador de US $ 50.450 por meio da Lei autorizada do Ministrio da Energia do governo russo Chambers.<br><strong style="color:#030E77;">Etapa 5.</strong> O vendedor envia os documentos de Prova parcial dos Produtos por meio de um Correio vinculado ao comprador para apresentar ao seu banco para abrir o DLC conforme listado abaixo:<br>(i) Contrato legalizado para transaes bem-sucedidas de 12 meses.<br>(ii) Certificado de Transao de Alocao em favor do Comprador, emitido pelo Ministrio de Energia da Rssia.<br>(iii) Certificado de Atestado emitido ao Vendedor pela Cmara de Comrcio Russa.<br>(iv) Certificado de Filiao emitido ao Vendedor pela Cmara de Comrcio Russa.<br>(v) Declarao de Disponibilidade do Produto emitida ao Comprador pelo Ministrio da Energia.<br>(vi) Atestado de Alocao do produto emitido ao Comprador pelo Ministrio da Energia.<br>(vii) Certificado de origem do produto emitido pela Cmara de Comrcio Russa.<br>(viii) Cpia da Licena de Exportao do Vendedor.<br>(ix) Cpia da Licena de Transporte do Vendedor.<br>(x) Cpia do Certificado de Constituio.<br>(xi) Passaporte de transao / negcio do produto.<br>(xii) Recibos do tanque e cpia do Contrato de Armazenamento.<br><strong style="color:#030E77;">Etapa 6.</strong> O comprador emite a Carta de Crdito Documentria MT700 (50 principais bancos ou confirmada por um banco dos 50 principais) ao Banco do Vendedor, pagvel contra a apresentao dos documentos de envio em conformidade com os termos e condies da Carta de Crdito e uma cpia do Aviso de Preparao (NOR) emitido pelo comandante do navio ou agente de transporte:<br>(i) Fatura Comercial - um original e trs cpias.<br>(ii) Conjunto (s) completo (s) de 3/3 dos conhecimentos de embarque originais \'limpos a bordo\' mais 3 cpias no negociveis, cada um emitido ou endossado para o pedido e, marcado: \'frete a pagar de acordo com o fretamento\'.<br>(iii) Certificado de anlise de qualidade emitido pela SGS - um original e trs cpias.<br>(iv) Certificado de origem emitido pela Cmara de Comrcio da Federao Russa - um original e trs cpias<br><strong style="color:#030E77;">Etapa 7.</strong> O POP  recebido pelo Comprador e apresentado ao funcionrio do banco<br>O oficial do banco do comprador abrir o DLC MT700<br>O vendedor carrega a carga e emite QQ pela SGS<br>Assim que o navio chegar ao porto de destino, o comprador realiza QQ pela SGS ou similar e libera o pagamento pelo MT 103.<br><br><strong style="color:#030E77;">FOB (HOUSTON/ROTTERDAN)</strong><br><p><strong style="color:#030E77;">Etapa 1.</strong> Comprador emite ICPO oficial - Pedido de Compra Corporativo Irrevogvel ao Vendedor e envia POF (Prova de fundos) MT799 / BCL / RWA; o ICPO deve ser emitido com o Papel Timbrado da Empresa do Comprador contendo todas as informaes de contato do Comprador.<br><strong style="color:#030E77;">Etapa 2.</strong> O vendedor verifica o ICPO oficial do Comprador e emite a Minuta do Contrato e a Fatura Proforma da primeira entrega ao Comprador.<br><strong style="color:#030E77;">Etapa 3.</strong> O comprador assina a minuta do contrato e a fatura pr-forma e a devolve com uma carta de aceitao, incluindo uma cpia da licena da empresa e uma cpia digitalizada do passaporte do comprador.<br><strong style="color:#030E77;">Passo 4.</strong>                A Vendedora d incio ao processo de legalizao e notarizao dos contratos via escritrio de advocacia. Processo para Documento de Produto / processo para Certificado de Transao de Alocao em favor do Comprador como o beneficirio legtimo com encargos legais arcados pelo Comprador de US $ 54.720 por meio das Cmaras de Legislao Autorizada do Ministrio de Energia do Governo Russo.<br><strong style="color:#030E77;">Etapa 5.</strong> Problema do vendedor para o comprador os seguintes documentos POP:<br>Certificado de Contrato Registrado e Passaporte de Atribuio emitido pelo Ministrio da Energia.<br>Relatrio SGS. (S BANCO PARA BANCO)<br>Certificado de Ttulo.<br>Autorizao Dip-Test. (A REFINARIA REALIZA O TESTE DE MERGULHO ATRAVS DO SURVEYOR, ENCAMINHA OS RESULTADOS AO BANCO)<br>Receitas da fazenda de tanques.<br>Acordo de Manifesto de Tanque, Porto de Armazenamento.<br>Cpia de aprovao para exportao.<br>Cpia da Licena do Vendedor para Exportar.<br>Cpia da Declarao de Disponibilidade do produto.<br>Cpia do Compromisso da Refinaria para produzir o produto.<br>Cpia do Contrato da Transneft e Instalao de Armazenamento para transporte do produto at o porto de carregamento.<br>Cpia dos acordos de fretamento para transportar o produto at o porto de descarga.<br>Conhecimento de Embarque.<br><strong style="color:#030E77;">Etapa 6.</strong> Aps a confirmao do SGS - Teste de Imerso, o Comprador paga com TT- Transferncia Telegrfica ou MT103 ao Vendedor pela quantidade elevvel.<br><strong style="color:#030E77;">Etapa 7.</strong> O comprador levanta o produto conforme acordado no contrato.</p><br>',imagen:'20.jpg'  },
