          { icono:'mdi-cogs',text: 'Procedimentos',module:'PROCEDURES2' , titulo:'Comprador emite uma Carta de Intenes - LOI via Banimento do Comprador',contenido:'<strong style="color:#030E77;">PROCEDIMENTOS</strong><BR><BR><BR>1. O Comprador emite uma Carta de Intenes - LOI por meio do Banco do Comprador.<BR>2. O vendedor recebe a LOI e emite uma oferta corporativa completa (FCO)<BR>3. O comprador aceita e assina o FCO e emite o Pedido de Compra Corporativo Irrevogvel (ICPO) com Carta de Confirmao Bancria (BCL) assinada por dois funcionrios do banco com verborragia (rascunho) de Carta de Crdito Documentrio (DLC) ou Carta de Crdito Standby (SBLC) ) ou MT799 para aprovao.<BR>4. O Vendedor emite o Acordo de Compra e Venda (SPA) - Contrato.<BR>5. O comprador assina, sela e devolve o SPA com alteraes / modificaes, se necessrio; o vendedor analisa o contrato, assina e o sela e o devolve ao comprador.<BR>6. O Vendedor e o Comprador trocam o SPA assinado por transmisses eletrnicas em pdf como cpias impressas e o vendedor emite a Fatura Proforma (PI).<BR>7. O banco do comprador emite o SBLC / DLC MT760 CONFIRMADO, IRREVOCVEL, TRANSFERVEL e DIVISVEL. Pagamento contra documento de embarque, no porto de embarque, emitido ou confirmado junto aos 50 principais bancos principais internacionais com valor de embarque de 1 (um) ms.<BR>8. No prazo de 25 (vinte e cinco) dias o produto  entregue no porto para embarque.<BR>9. O Comprador  convidado a monitorar o processo de carregamento do produto no navio e a certificao SGS.<BR>10. Depois de carregado, o banco do Vendedor emite os documentos de envio banco a banco.<BR>11. O banco do comprador emite o instrumento de pagamento em formato de 100%  vista MT 103, pagamento incondicional contra documento de embarque, no porto de carga, no prazo de 2 dias teis.',imagen:'13.jpg' },
